<template>
  <div>
    <loading-component :value="loading" />
    <v-timeline :dense="$vuetify.breakpoint.smAndDown">
      <v-timeline-item small v-for="(survey, index) in surveys" :key="index">
        <v-card class="elevation-2">
          <v-card-title class="headline">{{ survey.title }}</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            {{ survey.description }}
          </v-card-text>
          <v-card-actions>
            <v-btn class="primary">
              <router-link
              tag="div"
                :to="{ name: 'TakeSurvey', params: { id: survey.id } }"
                >Take it</router-link
              >
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-timeline-item>
    </v-timeline>
  </div>
</template>
<script>
import LoadingComponent from "@/components/LoadingComponent";

export default {
  name: "surveys",
  components: { LoadingComponent },
  data() {
    return {
      surveys: [],
      agent_id: this.$store.state.agent_id,
      loading: false,
    };
  },
  mounted() {
    this.loading = true;
    this.axios.get(this.api.ACTIONS.SURVEY).then((res) => {
      this.surveys = res.data.data.data;
      this.loading = false;
    });
  },
};
</script>
